<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query">
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column label="@" type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"
              ></el-tag
            >
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              @click="showBox(scope.row)"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-position"
              circle
              @click="showProgressBox"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showBoxDialogVisible"
      width="50%"
      @close="showBoxClose"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <el-form-item label=":" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="showBoxDialogVisible = false"> </el-button>
        <el-button type="primary" @click="showBoxDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
    >
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="progressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="progressDialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!-- -- -->
  </div>
</template>

<script>
import cityData from '@/plugins/citydata.js'
export default {
  name: 'order-',
  data () {
    return {
      showBoxDialogVisible: false,
      progressDialogVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [{ required: true, message: '', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      cityData,
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      total: 0
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    async getOrderList () {
      const { data: res } = await this.$axios.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(res)
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showBox (row) {
      this.showBoxDialogVisible = true
      console.log(row)
    },
    showBoxClose () {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    showProgressBox () {
      // const { data: res } = await this.$axios.get('/kuaidi/8084909574412544580')
      // console.log(res.data)
      this.progressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped></style>
